import Scroll from '../../../base/scroll/scroll'
import Coupons from '../../../base/coupons/coupons'
export default {
  name: "userMessageInfo",
  components: {
    Scroll,
    Coupons
  },
  created(){
    if(this.$route.params.message){
      this.message = this.$route.params.message
    }else{
      this.$router.push({ name: 'userMessage'})
    }
  },
  data() {
    return {
      message:''
    }
  }
}



